.ivanova {
    padding-top: 65px;
    .container {
        @extend %container;
    }
    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        gap: 10px;
        @include media (md) {
            grid-template-columns: 584px 1fr;
            grid-template-rows: 1fr 180px;
            grid-row-gap: 40px;
            grid-column-gap: 135px;
            padding-bottom: 50px;
            border-bottom: 0.5px solid #C7C7C7;
        }
    }
    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include media (md) {
            order: 1;
        }
    }
