    &__content-two {
        @include media (md) {
            order: 1;
        }
    }
    &__text {
        @include text (16px, 400, 24px, $secondaryColor);
        margin-bottom: 22px;
    }
    &__text-start {
        margin-bottom: 40px;
    }
    &__link-read {
        margin-top: 22px;
        position: relative;
    }
    &__link-read::after {
        content: "";
        background-image: url(../images/photocards/arrow.svg);
        display: block;
        width: 11px;
        height: 6px;
        position: absolute;
        top: 7px;
        right: -15px;
    }
}